import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import { Marker,List } from './Marker';
import { AngularFireAuth } from 'angularfire2/auth';
/**
 * Generated class for the MapPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

declare var google: any;

@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

   @ViewChild('mapCanvas') mapElement: ElementRef;

   map: any;
   ActiveMarker: any=null;
   Lat: any= 17.470229;
   Long: any =78.384921;
   LatLng: any;

   DatabaseReference: any=null; 
   IntervalHandle: any=null;
   MarkerClass: any;
   ListClass: any;

   KeyPassed: string=null;

  
  constructor(public Database:AngularFireDatabase,public navCtrl: NavController, public navParams: NavParams,private auth: AngularFireAuth) {
  
  this.KeyPassed = this.navParams.get('Key');
  console.log(this.KeyPassed + ' Key Passed');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MapPage');
    this.initMap();
    this.InitializeMarkerPosition();
    this.AttachDatabaseEvent();
    //this.OverRideBackButtonBehavior();
  //  this.MarkerClass= new Marker();
  // this.ListClass= new List<Marker>();
  }


  initMap(){
    let mapEle= this.mapElement.nativeElement;
    this.map= new google.maps.Map(mapEle,{zoom : 14,
                                          center: {lat: 17.437098,lng: 78.386266}
                                        });
   var marker= new google.maps.Marker({position : {lat: 17.437098,lng: 78.386266},
                                        map:this.map,
                                        title: "You are here!"
                                      });

   google.maps.event.addListenerOnce(this.map, 'idle', () => {
          mapEle.classList.add('show-map');
  })
  }

  InitializeMarkerPosition(){
    var DatabaseRef= this.Database.database.ref('/cabs'+'/'+this.KeyPassed.toString());
    DatabaseRef.once('value',function(snapshot){
      console.log(snapshot);
    })
  }
  
  AttachDatabaseEvent()
  { 
     var ChangedValue;
     var KeyValue=new String();
      this.DatabaseReference = this.Database.database.ref('/cabs'+'/'+this.KeyPassed.toString());
      this.DatabaseReference.on('child_changed',function(snapshot){
       ChangedValue= snapshot.val();
       KeyValue=snapshot.key.toString();
      // console.log(snapshot);
       if(!KeyValue.localeCompare('geolocation')){
         console.log('Location recieved '+ChangedValue.latitude+' '+ ChangedValue.longitude);
         this.Lat=ChangedValue.latitude;
         this.Long=ChangedValue.longitude;
       }
       //console.log(snapshot.key);
       console.log('geolocation'+ ChangedValue);
     });
  }

  GoBack(){
      this.DatabaseReference.off();
      this.DatabaseReference=null;
      this.navCtrl.pop();
      console.log('Going Back');
  }

  SetMarker()
  {
    var Marker;
    var image = 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png';
    Marker = new google.maps.Marker({
    position: {lat: this.Lat,lng: this.Long},
      map: this.map,
      icon: image
       });
    this.ActiveMarker=Marker;
  }

  IncrementPosition()
  {
    this.Lat-=0.001;
    this.Long-=0.001;
    console.log("Incrementing position");
  }

  DecrementPosition()
  {
    this.Lat+=0.001;
    this.Long+=0.001;
    console.log("Decrementing position");
  }

  RemoveMarker()
  {
    if(this.ActiveMarker!=null)
    {
    this.ActiveMarker.setMap(null);
    }
    console.log("Removing Marker");
  }

  StartJourney()
  {
    this.IntervalHandle=setInterval(() => {
      this.StartMovement();
    },500);
  }

  StartMovement()
  {
    console.log("Inside Start movement setposition");
    if(this.ActiveMarker==null)
    {
      console.log('Inside if');
      this.SetMarker();
    }
    else{
      console.log('Inside else');
      this.IncrementPosition();
      this.ActiveMarker.setPosition({lat: this.Lat,lng: this.Long});
    }
  }

  StartMovement2()
  {
    console.log("Inside start movement2");
    this.RemoveMarker();
    this.SetMarker();
    this.IncrementPosition(); 
  }

  StopMovement()
  {
    //this.Run=false;
    if(this.IntervalHandle!=null)
    {
      clearInterval(this.IntervalHandle);
    }
  }

  signOut() 
  {
    this.auth.auth.signOut();
  }

}





